html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}

a {
  font-size: 1.5rem;
  list-style: none;
}

.general-container {
  max-width: 85%;
  margin: 0 auto;
  overflow: hidden;
}

.semibold {
  font-weight: 600;
}

.bold {
  font-weight: 700;
}

.sizing {
  font-size: 2.3rem;
  letter-spacing: 1px;
  word-spacing: 2px;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  padding-bottom: 3rem;
}

.simple-button {
  background-color: transparent;
  color: #ffffff;
  border: none;
}

.link {
  color: #028aff;
  text-decoration: dashed;
  display: inline-block;
}

.white-btn {
  background-color: #ffffff;
  border: solid 1px #E2E2E2;
  padding: 8px 10px;
  border-radius: 5px;
}
.white-btn:hover {
  background-color: #febe3d;
  color: #ffffff;
  transition: 1s transform;
  transform: scale(1.06);
}

.blue-btn {
  background-color: #028aff;
  border: none;
  padding: 8px 10px;
  border-radius: 5px;
  color: #ffffff;
}
.blue-btn:hover {
  transition: 1s transform;
  transform: scale(1.1);
}

.button-download {
  background-color: #000000;
  padding: 1rem;
  width: 19rem;
  height: 7rem;
  color: #ffffff;
  border-radius: 1rem;
  display: grid;
  grid-template-columns: 20% 80%;
  align-items: center;
}
.button-download p {
  line-height: 1.2;
  margin-bottom: 0;
}

.green {
  background-color: #5ccaa7;
  padding: 1rem 1.5rem;
  display: block;
  border-radius: 1rem;
  text-align: center;
  color: #ffffff;
  font-size: 1.4rem;
}

.uppercase {
  text-transform: uppercase;
  font-size: 1.2rem;
}

.skew {
  background-color: #ffffff;
  width: 100.1%;
  height: 170px;
  position: absolute;
  bottom: -1px;
  clip-path: polygon(100% 50%, 0 100%, 100% 100%);
}

.skew2 {
  background-color: #171347;
  width: 100%;
  height: 70px;
  position: absolute;
  top: -69px;
  clip-path: polygon(100% 50%, 0 100%, 100% 100%);
}

.general-target {
  background-color: #ffffff;
  border-radius: 1rem;
}

.shadow {
  box-shadow: 0px 0px 10px 6px #E2E2E2;
}

.orange-tag,
.green-tag,
.blue-tag {
  padding: 1rem 1.5rem;
  color: #ffffff;
  display: inline-block;
  border-radius: 2rem;
  margin-bottom: 2rem;
}

.orange-tag {
  background-color: #febe3d;
}

.green-tag {
  background-color: #5ccaa7;
}

.blue-tag {
  background-color: #028aff;
}

@media (min-width: 992px) {
  .animacion1 {
    transition: transform 1s;
    -o-transition: transform 1s;
    -webkit-transition: transform 1s;
    -moz-transition: transform 1s;
    transform: translate(-600px);
    -o-transform: translate(-600px);
    -moz-transform: translate(-600px);
    -webkit-transform: translate(-600px);
  }
}

@media (min-width: 992px) {
  .animacion2 {
    transition: transform 1s;
    -o-transition: transform 1s;
    -webkit-transition: transform 1s;
    -moz-transition: transform 1s;
    transform: translate(700px);
    -o-transform: translate(700px);
    -moz-transform: translate(700px);
    -webkit-transform: translate(700px);
  }
}

#header {
  max-width: 120rem;
  background-color: #028aff;
  margin: 0 auto;
  padding: 3rem 0 5rem 0;
  position: relative;
}
@media (min-width: 540px) {
  #header {
    max-width: 54rem;
  }
}
@media (min-width: 768px) {
  #header {
    max-width: 76rem;
  }
}
@media (min-width: 992px) {
  #header {
    max-width: 99rem;
  }
}

#menu {
  display: grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas: "logo login" "navigation navigation";
  border-bottom: solid 1px #E2E2E2;
  align-items: center;
}
@media (min-width: 540px) {
  #menu {
    grid-template-columns: 55% 45%;
  }
}
@media (min-width: 768px) {
  #menu {
    display: flex;
    justify-content: space-between;
  }
}

#logo {
  grid-area: logo;
  width: 6rem;
  height: 3rem;
}
#logo img {
  width: 100%;
}
@media (min-width: 768px) {
  #logo {
    width: 8rem;
    height: 4rem;
  }
}
@media (min-width: 992px) {
  #logo {
    width: 10rem;
    height: 5rem;
  }
}

#principal-navigation {
  grid-area: navigation;
}
#principal-navigation ul {
  display: flex;
  width: auto;
  height: 50px;
  margin: 0;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
#principal-navigation ul a {
  color: #ffffff;
}
@media (min-width: 768px) {
  #principal-navigation {
    width: 35rem;
    justify-content: space-around;
  }
}
@media (min-width: 992px) {
  #principal-navigation {
    max-width: 50rem;
  }
}

#botones-menu {
  grid-area: login;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
@media (min-width: 768px) {
  #botones-menu {
    width: 17rem;
  }
}
@media (min-width: 992px) {
  #botones-menu {
    width: 19rem;
  }
}

#contenedor-principaltxt {
  margin: 5rem auto;
}
@media (min-width: 768px) {
  #contenedor-principaltxt {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 6rem;
  }
}

#principal-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 4rem;
}
#principal-text h1 {
  margin-top: 0;
  color: #ffffff;
  font-size: 4rem;
}
#principal-text p {
  text-align: justify;
  color: #ffffff;
}
#principal-text button {
  margin: 2rem 0;
  width: 15rem;
}
@media (min-width: 768px) {
  #principal-text {
    padding-bottom: 0;
  }
  #principal-text h1 {
    width: 25rem;
  }
  #principal-text p {
    width: 25rem;
  }
}
@media (min-width: 992px) {
  #principal-text #icons1 {
    display: flex;
    justify-content: space-between;
  }
}

#icons1 div {
  display: flex;
  justify-content: space-between;
}
#icons1 div img {
  width: 16%;
}

#target {
  border-bottom-right-radius: 5rem;
  width: 95%;
  margin: 0 auto 2rem;
}
@media (min-width: 768px) {
  #target {
    width: 100%;
  }
}
#target #cabezal {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1rem;
  padding-top: 2rem;
}
#target #card {
  margin: 0 auto 2rem;
  padding: 1.5rem 2rem;
  background-color: #171347;
  width: 95%;
  height: auto;
  border-radius: 1rem;
}
@media (min-width: 540px) {
  #target #card {
    width: 80%;
  }
}
@media (min-width: 768px) {
  #target #card {
    width: 95%;
  }
}
#target #card #logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
#target #card #logo img {
  width: 15%;
}
#target #card #logo p {
  margin: 0;
}
#target #card p {
  color: #ffffff;
}
#target #details #number-card {
  font-size: 1.6rem;
  word-spacing: 0.9rem;
  margin-top: 0.5rem;
}
@media (min-width: 540px) {
  #target #details #number-card {
    font-size: 1.9rem;
    letter-spacing: 0.2rem;
    word-spacing: 0.3rem;
  }
}
@media (min-width: 768px) {
  #target #details #number-card {
    font-size: 1.6rem;
    word-spacing: 0.1rem;
    letter-spacing: 0.1rem;
  }
}
@media (min-width: 992px) {
  #target #details #number-card {
    font-size: 2rem;
    word-spacing: 0.8rem;
  }
}
#target #name-date {
  width: 24rem;
  display: flex;
  justify-content: space-between;
}
#target .name-date {
  margin-top: 0;
}
#target .link p {
  padding: 2rem;
}

#buy-one, #buy-two {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas: "producto costo" "descripcion descripcion";
  border-bottom: solid 1px #E2E2E2;
}
#buy-one .product, #buy-two .product {
  margin-top: 2rem;
  grid-area: producto;
  margin-bottom: 0;
}
#buy-one .cost, #buy-two .cost {
  margin-top: 2rem;
  grid-area: costo;
  display: inline-block;
  text-align: end;
  margin-bottom: 0;
}
#buy-one .description, #buy-two .description {
  margin-top: 1rem;
  grid-area: descripcion;
  color: #727f95;
}

#main {
  max-width: 1200px;
  margin: 5rem auto;
}

.row {
  width: 80%;
  padding-bottom: 5rem;
}

.img-video {
  position: relative;
}
.img-video .contenedor-imagen {
  position: relative;
}
.img-video img {
  position: absolute;
  border-radius: 1.5rem;
}

#row1 {
  margin: 1rem auto;
}
@media (min-width: 992px) {
  #row1 {
    margin: 5rem auto;
    display: grid;
    align-items: center;
    grid-template-columns: 60% 40%;
  }
}
#row1 .img-video {
  position: relative;
  width: 90%;
}
#row1 .contenedor-imagen {
  width: 22rem;
  position: absolute;
  top: -5rem;
  right: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  #row1 .contenedor-imagen {
    width: 26rem;
  }
}
#row1 .contenedor-imagen img {
  width: 100%;
}
#row1 .contenedor-imagen button {
  position: absolute;
  margin: 1rem;
  right: 0;
}
#row1 .information {
  margin-top: 2rem;
}
@media (min-width: 992px) {
  #row1 .information {
    translate: 600px;
  }
}

#card-description {
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
}
@media (min-width: 540px) {
  #card-description {
    width: 90%;
  }
}
@media (min-width: 768px) {
  #card-description {
    width: 70%;
  }
}
#card-description .lines {
  display: grid;
  grid-template-columns: 70% 30%;
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas: "desc hour" "linea linea";
}
@media (min-width: 768px) {
  #card-description .lines {
    grid-template-columns: 80% 20%;
  }
}
#card-description .lines .desc-line {
  grid-area: desc;
  padding-bottom: 0;
}
#card-description .lines .hour {
  grid-area: hour;
  padding-bottom: 0;
  text-align: end;
  color: #727f95;
}
#card-description .lines div {
  grid-area: linea;
}

#row2 {
  display: grid;
  grid-template-rows: 23rem 20rem;
  justify-items: center;
  margin: 3rem auto;
}
@media (min-width: 540px) {
  #row2 {
    grid-template-rows: 30rem 20rem;
  }
}
@media (min-width: 768px) {
  #row2 {
    grid-template-rows: 34rem 20rem;
  }
}
#row2 .img-video {
  width: 90%;
}
#row2 .img-video button {
  position: absolute;
  margin: 1rem;
  left: 0;
}
#row2 .contenedor-imagen {
  width: 22rem;
  margin: 0 auto;
}
@media (min-width: 540px) {
  #row2 .contenedor-imagen {
    width: 30rem;
  }
}
@media (min-width: 768px) {
  #row2 .contenedor-imagen {
    width: 38rem;
  }
}
#row2 .contenedor-imagen img {
  width: 100%;
}
@media (min-width: 992px) {
  #row2 {
    margin: 5rem auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    justify-items: end;
  }
  #row2 .information {
    margin-top: 4rem;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
@media (min-width: 992px) and (min-width: 992px) {
  #row2 .information {
    translate: -700px;
  }
}
@media (min-width: 992px) {
  #row2 .img-video {
    grid-column: 2/3;
    width: 35rem;
  }
}

.checked {
  display: flex;
  gap: 1rem;
}
.checked .check {
  background-color: #5ccaa7;
  width: 2rem;
  height: 2rem;
  border-radius: 0.5rem;
}
.checked p {
  color: #5ccaa7;
}

#row3 {
  margin: 6rem auto;
}
@media (min-width: 768px) {
  #row3 {
    margin: 4rem auto;
  }
}
@media (min-width: 992px) {
  #row3 {
    display: grid;
    align-items: center;
    grid-template-columns: 50% 50%;
    column-gap: 5rem;
  }
}
#row3 .information {
  margin-top: 3rem;
}
@media (min-width: 992px) {
  #row3 .information {
    translate: 600px;
  }
}

#card-3 {
  margin: 0 auto;
}
@media (min-width: 540px) {
  #card-3 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 2rem;
  }
}
#card-3 #personal-card {
  width: 20rem;
}
#card-3 #personal-card img {
  width: 100%;
}

#contenedor-tarjeta-personal {
  display: flex;
  flex-direction: column;
  grid-column: 1/2;
  grid-row: 1/3;
  gap: 2rem;
}

#personal-information {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  padding: 2rem 1rem;
}
#personal-information #iniciales {
  background-color: #febe3d;
  width: 3rem;
  height: 3rem;
  border-radius: 3rem;
  color: #ffffff;
}
#personal-information #iniciales p {
  text-align: center;
  margin-bottom: 0;
  line-height: 1.5;
}
#personal-information #nombre {
  margin-bottom: 0;
  text-align: center;
  line-height: 2;
}

#component {
  padding: 2rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
#component #paloma {
  background-color: #5ccaa7;
  width: 4rem;
  height: 4rem;
  border-radius: 3rem;
}
#component #desc {
  font-size: 1.4rem;
  color: #727f95;
  margin-bottom: 0;
}

.airbnb {
  grid-column: 2/3;
  grid-row: 1/2;
  width: 100%;
  padding: 1rem 2rem;
  height: auto;
  text-align: center;
}
.airbnb .contenedor-logo-airbnb {
  width: 10rem;
  margin: 1rem auto;
}
.airbnb .contenedor-logo-airbnb img {
  width: 100%;
}
.airbnb .stars {
  margin-bottom: 1rem;
}
.airbnb p {
  margin-bottom: 0.5rem;
}

#company {
  grid-column: 2/3;
  grid-row: 2/3;
  text-align: center;
}
#company #contenedor-logo-company {
  width: 5rem;
  margin: 2rem auto 1rem auto;
}
#company #contenedor-logo-company img {
  width: 100%;
  border-radius: 5rem;
}

.photos {
  margin: 0 auto;
  width: 70%;
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;
  padding-bottom: 1rem;
  border-bottom: solid 1px #E2E2E2;
}
.photos #p-1,
.photos #p-2,
.photos #p-3 {
  width: 2.5rem;
  height: 2.5rem;
}
.photos #p-1 img,
.photos #p-2 img,
.photos #p-3 img {
  width: 100%;
  border-radius: 2rem;
}

#icons {
  margin: 0 auto;
  width: 70%;
  display: flex;
  justify-content: space-around;
}

#row4 {
  display: grid;
  grid-template-rows: 22rem 38rem;
  justify-items: center;
  margin: 5rem auto;
}
@media (min-width: 540px) {
  #row4 {
    grid-template-rows: 26rem 28rem;
  }
}
@media (min-width: 768px) {
  #row4 {
    grid-template-rows: 33rem 27rem;
  }
}
#row4 .img-video {
  width: 90%;
}
#row4 .img-video button {
  position: absolute;
  margin: 1rem;
  right: 0;
}
@media (min-width: 992px) {
  #row4 .img-video {
    grid-row: 1/2;
    grid-column: 2/3;
  }
}
#row4 .contenedor-imagen {
  width: 22rem;
  margin: 0 auto;
}
@media (min-width: 540px) {
  #row4 .contenedor-imagen {
    width: 30rem;
  }
}
@media (min-width: 768px) {
  #row4 .contenedor-imagen {
    width: 38rem;
  }
}
#row4 .contenedor-imagen img {
  width: 100%;
}
#row4 .information {
  grid-row: 2/3;
  display: grid;
  grid-template-rows: 17rem 21rem;
  align-items: center;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  #row4 .information {
    grid-template-rows: 17rem 11rem;
  }
}
@media (min-width: 992px) {
  #row4 .information {
    translate: -700px;
    grid-template-rows: 17rem 8rem;
    grid-row: 1/2;
    grid-column: 1/2;
  }
}
#row4 .information #information-txt {
  grid-column: 1/2;
  grid-row: 1/2;
}
#row4 .information #btns-row4 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  #row4 .information #btns-row4 {
    flex-direction: row;
    justify-content: space-around;
    grid-column: 1/2;
    grid-row: 2/3;
    width: 90%;
  }
}
@media (min-width: 992px) {
  #row4 .information #btns-row4 {
    width: 100%;
  }
}
@media (min-width: 992px) {
  #row4 {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 30rem;
    justify-items: end;
  }
}

#solution {
  background-color: #171347;
  border-top-right-radius: 3rem;
  border-top-left-radius: 3rem;
  height: auto;
  color: #ffffff;
  padding: 2rem;
}
@media (min-width: 992px) {
  #solution {
    padding: 5rem;
  }
}
#solution h3 {
  padding-bottom: 0;
  border-top-right-radius: 7rem;
  border-top-left-radius: 7rem;
}

@media (min-width: 992px) {
  #involve {
    display: grid;
    grid-template-columns: 50% 50%;
    justify-items: end;
  }
}
#involve button {
  width: 15rem;
}
@media (min-width: 992px) {
  #involve button {
    width: 22rem;
  }
}

#section-awards {
  margin-bottom: 2rem;
}

#awards {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 540px) {
  #awards {
    margin: 2rem auto;
    width: 80%;
  }
}
@media (min-width: 768px) {
  #awards {
    width: 100%;
    display: grid;
  }
}
@media (min-width: 992px) {
  #awards {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}
#awards .airbnb,
#awards .amazon,
#awards .slack,
#awards .spotify,
#awards .paypal {
  width: 90%;
  cursor: pointer;
}
#awards .airbnb:hover,
#awards .amazon:hover,
#awards .slack:hover,
#awards .spotify:hover,
#awards .paypal:hover {
  transition: 1s transform;
  transform: scale(1.1);
}

.amazon {
  text-align: center;
  padding: 2rem;
  height: auto;
}
.amazon .contenedor-logo-amazon {
  width: 10rem;
  margin: 2rem auto;
}
.amazon .contenedor-logo-amazon img {
  width: 100%;
}

.slack {
  text-align: center;
  padding: 2rem;
  height: auto;
}
.slack .contenedor-logo-slack {
  width: 12rem;
  margin: 2rem auto;
}
.slack .contenedor-logo-slack img {
  width: 100%;
}

.spotify {
  padding: 2rem;
  height: auto;
  text-align: center;
}
.spotify .contenedor-logo-spotify {
  width: 13rem;
  margin: 2rem auto;
}
.spotify .contenedor-logo-spotify img {
  width: 100%;
}
.spotify .stars {
  margin-bottom: 1rem;
}
.spotify p {
  margin-bottom: 0.5rem;
}

.paypal {
  padding: 2rem;
  height: auto;
  text-align: center;
}
.paypal .contenedor-logo-paypal {
  width: 13rem;
  margin: 2rem auto;
}
.paypal .contenedor-logo-paypal img {
  width: 100%;
}

#help {
  background-color: #FAFBFE;
  text-align: center;
  padding: 2rem 2rem 8rem 2rem;
}
#help a {
  padding: 0 2rem;
}

#accordion {
  width: 100%;
